import './Categorias.css';

import { FirebaseAnalytics } from '@capacitor-community/firebase-analytics';
import { Capacitor } from '@capacitor/core';
import { IonSpinner } from '@ionic/react';
import { observer } from 'mobx-react-lite';
import { SnapshotIn, SnapshotOut } from 'mobx-state-tree';
import React, { useEffect, useState } from 'react';
import { RouteComponentProps } from 'react-router';

import { Page } from '../../components';
import { ProductoCard } from '../../components/ProductoCard';
import { Link as SubheaderLinks, SubHeader } from '../../components/SubHeader';
import { RUTAS } from '../../constants';
import { formatTextoURL, useObtenerDatosInicio, useToaster } from '../../helpers-ts';
import { useStores } from '../../models/root-store/root-store-context';
import { Producto } from '../../models/types';
import { Categoria, CategoriaArbolModel } from '../../models/types/Categoria';


export interface iProducto extends SnapshotIn<typeof Producto> {}
type PageProps = {} & RouteComponentProps<{ id: string }>

export const Categorias: React.FC<PageProps> = observer(({ match, history }) => {
  const rootStore = useStores()
  const { categorias, categoriasWeb, buscarStore, loginStore, arbolFlat } = rootStore
  const Movil = rootStore.esMovil

  const [CategoriaFiltro, setCategoriaFiltro] = useState<SnapshotOut<typeof Categoria>>()
  const [ProductosCategoria, setProductosCategoria] = useState([] as iProducto[])
  const [Links, setLinks] = useState<SubheaderLinks[]>([{ titulo: 'Categorias', ruta: RUTAS.CATEGORIAS }])
  const [Pagina, setPagina] = useState(1)
  const [Cantidad, setCantidad] = useState<number>()
  const [CantidadPaginas, setCantidadPaginas] = useState<number>()
  const [Loading, setLoading] = useState(true)
  const addToast = useToaster()
  const cantidadPorPagina = 20
  const obtenerDatosInicio = useObtenerDatosInicio
  const [CategoriasHijos, setCategoriasHijos] = useState<SnapshotOut<typeof CategoriaArbolModel>[]>()
  const [MasVendidos, setMasVendidos] = useState([])

  useEffect(() => {
    if (categoriasWeb.length < 0) {
      obtenerDatosInicio()
    }
    const categoriaId = parseInt(match.params.id)
    setCategoriaFiltro(categorias.find((cat) => categoriaId === cat.id))
    obtenerProductos()
  }, [match])

  useEffect(() => {
    const links: SubheaderLinks[] = []
    if (CategoriaFiltro) {
      links.push({
        titulo: CategoriaFiltro?.nombre ? CategoriaFiltro?.nombre : '',
        ruta: CategoriaFiltro?.id ? RUTAS.CATEGORIA_LISTADO_PRODUCTOS(CategoriaFiltro?.id, formatTextoURL(CategoriaFiltro?.nombre)) : RUTAS.CATEGORIAS,
      })
      for (let i = 0; i < 1; i++) {
        const catPadre = categorias.find((cat) => CategoriaFiltro?.superior === cat.id)
        if (catPadre) {
          links.push({
            titulo: catPadre?.nombre ? catPadre.nombre : '',
            ruta: catPadre?.id ? RUTAS.CATEGORIA_LISTADO_PRODUCTOS(catPadre.id, formatTextoURL(catPadre.nombre)) : RUTAS.CATEGORIAS,
          })
        }

        const nodo = arbolFlat.find((cat) => cat.id === CategoriaFiltro.id)
        const hijos = nodo?.hijos
        if (hijos) {
          setCategoriasHijos(hijos)
        }
      }
    }
    links.push({ titulo: 'Categorías', ruta: RUTAS.CATEGORIAS })

    setLinks(links.reverse())
  }, [CategoriaFiltro])

  const registroEventoFirebase = (encontrado: boolean) => {
    const data = {
      page: 'busqueda',
      texto_busqueda: buscarStore.texto_busqueda,
      encontrado: encontrado,
    } as any
    if (Capacitor.getPlatform() === 'web') {
      const dataLayer = (window as any).dataLayer || []
      // @ts-ignore
      function gtag() {
        dataLayer.push(arguments)
      }
      if (loginStore.isLoggedIn) {
        data.usuario_id = loginStore.id
      }
      // @ts-ignore
      gtag('event', 'search_item', data)
    } else {
      FirebaseAnalytics.logEvent({
        name: 'page_view',
        params: data,
      })
        .then((res: any) => {
          console.log(res)
        })
        .catch((error: any) => {
          console.error(error)
        })
    }
  }

  const obtenerProductos = () => {
    const id_categoria = match.params.id
    let precioMinimo = ''
    let precioMaximo = ''

    const rango_precio = precioMinimo + '' + precioMaximo

    let categoria_filtro = ''

    let calificacion_filtro = ''

    let empresa_filtro = ''

    let rubro_filtro = ''

    setLoading(true)

    //set mas vendidos
    rootStore.environment.api
      .productosBuscador(
        Pagina,
        cantidadPorPagina,
        '',
        buscarStore.texto_busqueda,
        rango_precio,
        empresa_filtro,
        rubro_filtro,
        id_categoria,
        calificacion_filtro,
        true, // más vendidos
      )
      .then((result) => {
        if (result.kind === 'ok') {
          if (result.data.count === 0) {
            registroEventoFirebase(false)
          } else {
            registroEventoFirebase(true)
          }
          // @ts-ignore
          const cantidadPaginas = Math.ceil(result.data.count / cantidadPorPagina)
          setCantidadPaginas(cantidadPaginas)
          // @ts-ignore
          setCantidad(result.data.count)
          setMasVendidos(result.data.results as any)
        } else if (result.kind === 'not-found') {
          setCantidadPaginas(1)
          setCantidad(1)
          setPagina(1)
          rootStore.setProductosInicio([])
        } else {
          addToast('Ocurrió un error, vuelva a intentar en unos momentos', 'error')
        }
        setLoading(false)
      })
    //set otros
    rootStore.environment.api
      .productosBuscador(
        Pagina,
        cantidadPorPagina,
        '',
        buscarStore.texto_busqueda,
        rango_precio,
        empresa_filtro,
        rubro_filtro,
        id_categoria,
        calificacion_filtro,
        false,
      )
      .then((result) => {
        if (result.kind === 'ok') {
          if (result.data.count === 0) {
            registroEventoFirebase(false)
          } else {
            registroEventoFirebase(true)
          }
          // @ts-ignore
          const cantidadPaginas = Math.ceil(result.data.count / cantidadPorPagina)
          setCantidadPaginas(cantidadPaginas)
          // @ts-ignore
          setCantidad(result.data.count)
          // Solamente los no destacados
          const productos = result.data.results.filter(prod => !MasVendidos.find((prod2: any) => prod2.id === prod.id))
          setProductosCategoria(productos)
        } else if (result.kind === 'not-found') {
          setCantidadPaginas(1)
          setCantidad(1)
          setPagina(1)
          rootStore.setProductosInicio([])
        } else {
          addToast('Ocurrió un error, vuelva a intentar en unos momentos', 'error')
        }
        setLoading(false)
      })
  }

  return (
    <Page login_required={false} solo_cliente={true}>
      <SubHeader Movil={Movil} links={Links} />
      <div
        className="page-wrapper container-lg pt-3"
        style={{ display: 'flex', flexDirection: 'column', height: 'fit-content' }}
      >
        {!!CategoriasHijos && CategoriasHijos.length > 0 && (
          <div className="categorias-hijas-container">
            <div className={'d-flex pb-1'}>
              {CategoriasHijos.map((categoria, index) => (
                <div className={'categoria-hija'} key={index}>
                  <a
                    className="categoria-relacionada-slide"
                    onClick={() => history.push(RUTAS.CATEGORIA_LISTADO_PRODUCTOS(categoria.id, formatTextoURL(categoria.nombre)))}
                  >
                    <span>{categoria.nombre}</span>
                  </a>
                </div>
              ))}
            </div>
          </div>
        )}
        {!CategoriaFiltro && (
          <div className={'row container-categoria-pagina-listado'}>
            {categoriasWeb.filter(cat => !!cat.imagen).map((categoria) => {
              return <div className="col-6 col-lg-4">
                <div
                  className={'imagen-container-listado cpointer mb-3'}
                  style={{
                    backgroundImage: `url(${categoria.imagen})`,
                    backgroundSize: 'auto 100%',
                    display: 'flex',
                    alignItems: 'flex-end',
                  }}
                  onClick={() => history.push(RUTAS.CATEGORIA_LISTADO_PRODUCTOS(categoria.id, formatTextoURL(categoria.nombre)))}
                >
                  <div
                      className="gradiente-categoria"
                      style={{ flex: 1, display: 'flex', alignItems: 'flex-end', justifyContent: 'flex-start' }}
                  >
                    <span className="texto-tarjeta-categoria">{categoria.nombre}</span>
                  </div>
                </div>
              </div>
            })}
          </div>
        )}
        {!!CategoriaFiltro && MasVendidos.length > 0 && !Loading && (
          <>
            <h3 className={'text-center mt-2'}>Destacados</h3>
            <div className="productos-categoria-container row">
              {MasVendidos.map((producto: any, index: number) => (
                <ProductoCard
                  key={producto.id}
                  producto={producto}
                  index={index}
                  containerClass={'col-6 mb-4 col-lg-3'}
                />
              ))}
            </div>
          </>
        )}
        {!Loading && (
          <div className="productos-categoria-container row">
            {!!CategoriaFiltro && ProductosCategoria.length > 0 && ProductosCategoria.filter(prod => !MasVendidos.find((prod2: any) => prod2.id === prod.id)).map((producto, index) => (
              <ProductoCard
                key={producto.id}
                producto={producto}
                index={index}
                containerClass={'col-6 mb-4 col-lg-3'}
              />
            ))}
            {!!CategoriaFiltro && !ProductosCategoria.length && !MasVendidos.length && (
              <div className={'col-12 text-center'}>
                <p>No se encontraron productos</p>
              </div>
            )}
            {(!!CategoriaFiltro && (ProductosCategoria.length > 0 || MasVendidos.length > 0)) && <div className="py-2 col-12 text-center" onClick={() => {
              history.push(`${RUTAS.BUSCAR}?c=${CategoriaFiltro.id}`)
            }}>
              <span className="footer-carousel-productos">+ Ver más</span>
            </div>}
          </div>
        )}
        {Loading && (
          <div className="d-flex justify-content-center align-items-center" style={{ height: 400 }}>
            <IonSpinner />
          </div>
        )}
      </div>
    </Page>
  )
})

export default Categorias
